@import "../../_globalColor";

.lab-card-container {
  cursor: pointer;
}

.lab-card {
  position: relative;
  background: $darkBackground;
  border-radius: 1rem;
  box-shadow: 0 4px 12px $darkBoxShadow;
  padding: 1.5rem;
  transition: transform 0.3s ease;
  overflow: hidden;

  &:hover {
    transform: translateY(-5px);
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(
      circle at center,
      rgba(149, 76, 233, 0.15),
      transparent 70%
    );
    opacity: 0;
    transition: opacity 0.3s ease;
    z-index: 0;
  }

  &:hover::before {
    opacity: 1;
  }

  * {
    position: relative;
    z-index: 1;
  }

  .lab-title {
    font-size: 1.25rem;
    margin-bottom: 0.5rem;
  }

  .lab-description {
    font-size: 1rem;
    color: $textColorDark;
  }

  .go-corner {
    position: absolute;
    top: 0;
    right: 0;
    width: 32px;
    height: 32px;
    background-color: #9747ff;
    border-bottom-left-radius: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2;
  }

  .go-arrow {
    color: white;
    font-size: 1rem;
    font-weight: bold;
  }
}
